.recommended {
  &__product {
    grid-column: 1/-1;

    @include onTablet {
      grid-column: 2/-2;
    }

    @include onDesktop {
      grid-column: span 4;
    }
  }

  &__products {
    @include pageGrid;
    row-gap: 40px;
  }
}

.product img {
  transition: transform  0.3s;

  &:hover {
    transform: scale(1.1);
  }
}
